<template>
  <div class="g-order scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
    <div class="bit">
      <router-link :to="{name:'orderDetials',query:{orderid:v.id}}" tag="div" v-for="(v,i) in list" class="m-box" :key="i">
        <div class="s-title">
          <span class="timeMsg">下单时间：</span>
          <span class="time">{{v.createTimeStr}}</span>
          <b>{{v.state==1?'待支付':v.state==2?'待发货':v.state==3?'待确认收货':'已完成'}}</b>
        </div>
        <div class="s-content">
          <div class="img">
            <img v-lazy="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.proImg" alt="">
          </div>
          <div class="details">
            <div class="title beyond">{{v.proName}}</div>
            <div class="money">{{v.sortId==1||v.sortId==2?'积分':'元宝'}}：{{v.orderMoney}}</div>
            <div class="number">数量&nbsp;&nbsp;&nbsp;&nbsp;×{{v.proNumber}}</div>
          </div>
        </div>
        <div class="s-button">
          <div class="btned" @click.stop="confirming(v.id,i)" v-if="v.state==3">确认收货</div>
          <div class="btned" @click.stop="paying(v.id,i)" v-if="v.state==1">去支付</div>
          <div class="btned" @click.stop="finding(v.username)">联系客服</div>
        </div>
      </router-link>
    </div>
    </cube-scroll>
    <nocontent net="暂无订单~" v-show="list.length==0"/>
  </div>
</template>

<script>
  import nocontent from "@/components/nocontent"
  export default {
    name: "order",
    data(){
      return{
        list: [],/*列表数据*/
        options: {
          /*滚动配置*/
          scrollbar: {fade: true},
          pullUpLoad: {txt: {noMore: '没有更多数据了'}},
          pullDownRefresh: {stopTime: 0, txt: ' '}
        },
        bit:{
          username:sessionStorage.getItem('userid'),
          page:1
        }
      }
    },
    created() {
      this.orderList()
    },
    methods:{
      orderList(){
        this.$post(this.$bee+'/api/order/listOrder',this.bit).then((data)=>{
          if(data.code==200&&data.data.orders.length!=0){
            data.data.orders.forEach((i)=>{
              i.proImg=i.proImg.split(',')[0]
              this.list.push(i)
            })
            this.bit.page++;
          }else{
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      onPullingDown() {/*下拉刷新*/
        this.bit.page=1;
        this.list=[];
        this.orderList()
      },
      onPullingUp() {/*上拉加载*/
        this.orderList()
      },
      paying(v,i){/*立即支付*/
        this.$createDialog({
          type: 'confirm',
          icon: 'cubeic-alert',
          title: '是否确认支付',
          confirmBtn: {
            text: '确定支付',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            this.$post(this.$bee+'/api/order/payOrder',{username:this.bit.username,orderId:v}).then((data)=> {
              if (data.code == 200) {
                this.list[i].state=2;
              } else {
                this.$createToast({
                  txt: data.message,
                  type: 'txt'
                }).show();
              }
            })
          },
          onCancel: () => {
            console.log('取消')
          }
        }).show()
      },
      finding(v){/*联系客服*/
        this.$createDialog({
          type: 'alert',
          title: '蜂信号：'+v
        }).show()
      },
      confirming(_orderId,i){/*确认收货*/
        this.$createDialog({
          type: 'confirm',
          icon: 'cubeic-alert',
          title: '是否确认收货',
          confirmBtn: {
            text: '确定收货',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            this.$post(this.$bee+'/api/order/receiptOrder',
              {username:sessionStorage.getItem('userid'),orderId:_orderId}
            ).then((data)=> {
              if (data.code == 200) {
                this.list[i].state=5;
              } else {
                this.$createToast({
                  txt: data.message,
                  type: 'txt'
                }).show();
              }
            })
          },
          onCancel: () => {
            console.log('取消')
          }
        }).show()
      }
    },
    components: {
      nocontent
    }
  }
</script>

<style scoped lang="stylus">
.g-order
  .m-box
    border-bottom 7.5px solid #eaeaea
    &:last-child
      border 0
    .s-title
      border-bottom 1px solid #eaeaea
      padding 0 13px
      height 25px
      text-align left
      .timeMsg,.time,b
        font-size 13px
        display inline-block
        height 25px
        line-height 25px
        vertical-align text-top
      .timeMsg
        color #b4b4b4
      .time
        color #7e7e7e
      b
        color #f9524b
        float right
      span
        color #b4b4b4
    .s-content
      display flex
      flex-wrap wrap
      height 80px
      padding 0 13px
      text-align left
      padding-top 10px
      box-sizing border-box
      border-bottom 1px solid #eaeaea
      .img
        width 60px
        height 60px
        img
          width 100%
          height 100%
      .details
        width 255px
        padding-left 15px
        .title
          color #444444
          font-size 13px
          padding 5px 0
        .money
          color #cc5b24
          font-size 15px
          margin-top 9px
        .number
          font-size 13px
          color #989898
          margin-top 7px
    .s-button
      text-align right
      padding 0 13px
      height 28px
      .btned
        width 57.5px
        height 17.5px
        line-height 17.5px
        color #fff
        font-size 12px
        background #e65f16
        display inline-block
        text-align center
        margin-left 7px
        vertical-align: middle
</style>
